main{
    padding: 2.6rem;
}
.form{
    display: grid;
    grid-template: 40px 40px / 1fr 1fr;
    gap: 16px;
}

.form-button{
    grid-column: 1/-1;
    border-radius: 5px;
    background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
    color: white;
    border: none;
    cursor: pointer ;
}

.form-input{
    border-radius: 5px;
    border:1px solid #d5d4d8;
    text-indent: 5px;
}

.meme-image {
    display: block;
    margin: 1.5rem auto;
    width: 90%;
    max-height: 60vh;
    border-radius: 5px;
    
}

@media (min-width:500px){
    .meme-image {
      width: 50%; 
        
    }
}
.meme{
    position: relative;
}
.meme-text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}
.bottom {
    bottom: 0;
}

.top {
    top: 0;
}
